export const WS_CONNECTION_START = 'WS_CONNECTION_START';
export const WS_CONNECTION_SUCCESS = "WS_CONNECTION_SUCCESS";
export const WS_CONNECTION_ERROR = "WS_CONNECTION_ERROR";
export const WS_CONNECTION_CLOSED = "WS_CONNECTION_CLOSED";
export const WS_GET_MESSAGE = "WS_GET_MESSAGE";
export const WS_USER_CONNECTION_START = "WS_USER_CONNECTION_START";
export const WS_USER_CONNECTION_SUCCESS = "WS_USER_CONNECTION_SUCCESS";
export const WS_USER_CONNECTION_ERROR = "WS_USER_CONNECTION_ERROR";
export const WS_USER_CONNECTION_CLOSED = "WS_USER_CONNECTION_CLOSED";
export const WS_USER_GET_MESSAGE = "WS_USER_GET_MESSAGE";
const initialState = {
  wsConnected: false,
  wsError: undefined,
  messages: {
    orders: [],
    total: 0,
    totalToday: 0,
  },
  userMessages: {
    orders: [],
    total: 0,
    totalToday: 0,
  },
};
export const wsReducer = (state = initialState, action) => {
  switch (action.type) {
     //      WS_CONNECTION_SUCCESS
        //   wsConnected   true
        case WS_CONNECTION_SUCCESS:
            return {
              ...state,
              wsConnected: true,
              wsError: undefined,
            };
      
              //      WS_CONNECTION_ERROR
              //   wsConnected   false     action.payload
          case WS_CONNECTION_ERROR:
            return {
              ...state,
              wsConnected: false,
              wsError: action.payload,
            };
      
              //      WS_CONNECTION_CLOSED,   
              //   wsConnected   false
          case WS_CONNECTION_CLOSED:
            return {
              ...state,
              wsConnected: false,
              wsError: undefined,
            };
      
              //      WS_GET_MESSAGE
              //  ,     
              //  messages  ,    
          case WS_GET_MESSAGE:
            return {
              ...state,
              messages: {...action.payload}
            };
      
      //             
            case WS_USER_CONNECTION_SUCCESS:
              return {
                ...state,
                wsConnected: true,
                wsError: undefined,
              };
        
            case WS_USER_CONNECTION_ERROR:
              return {
                ...state,
                wsConnected: false,
                wsError: action.payload,
              };
        
            case WS_USER_CONNECTION_CLOSED:
              return {
                ...state,
                wsConnected: false,
                wsError: undefined,
              };
        
            case WS_USER_GET_MESSAGE:
              return {
                ...state,
                userMessages: {...action.payload}
            };
          default:
            return state;
        }
};
export default wsReducer;